$primary-font: "Roboto", "Segoe UI", "Oxygen", "Ubuntu", "Cantarell",
  "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;

/* roboto-100 - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 100;
  font-display: swap;
  src: local("Roboto Thin"), local("Roboto-Thin"),
    url("../../static/fonts/roboto-v20-latin-100.woff2") format("woff2"),
    /* Super Modern Browsers */
      url("../../static/fonts/roboto-v20-latin-100.woff") format("woff"),
    /* Modern Browsers */ url("../../static/fonts/roboto-v20-latin-100.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../../static/fonts/roboto-v20-latin-100.svg#Roboto") format("svg"); /* Legacy iOS */
}
/* roboto-regular - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto"), local("Roboto-Regular"),
    url("../../static/fonts/roboto-v20-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */
      url("../../static/fonts/roboto-v20-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("../../static/fonts/roboto-v20-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../../static/fonts/roboto-v20-latin-regular.svg#Roboto")
      format("svg"); /* Legacy iOS */
}
/* roboto-900 - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: local("Roboto Black"), local("Roboto-Black"),
    url("../../static/fonts/roboto-v20-latin-900.woff2") format("woff2"),
    /* Super Modern Browsers */
      url("../../static/fonts/roboto-v20-latin-900.woff") format("woff"),
    /* Modern Browsers */ url("../../static/fonts/roboto-v20-latin-900.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../../static/fonts/roboto-v20-latin-900.svg#Roboto") format("svg"); /* Legacy iOS */
}
