// colors
$black: #000000;
$white: #ffffff;
$red: #d52b1e;

$gold: #ffd700;
$light-blue: #4286f4;
$dark-blue: #275298;
$grey-2: #373b44;

// layout
$mobile-margin: 20px;
$tablet-margin: 60px;
$desktop-margin-sm: 5.85%;
$desktop-margin-lg: 8.33%;

$mobile-spacing: 20px;
$tablet-spacing: 40px;
$desktop-spacing: 60px;

// z-index
$z-zero: 0;
$z-base: 100;
$z-nav: 300;
$z-search: 400;
$z-player: 600;
$z-modal-bg: 900;
$z-modal: 1000;

// A map of breakpoints.
$breakpoints: (
  phone-sm: 420px,
  phone: 767px,
  tablet: 768px,
  tablet-lg: 1024px,
  desktop: 1025px,
  desktop-lg: 1600px,
);
